* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
  color: black;
}

@font-face {
  font-family: "Kingthings";
  src: url("../../../public/multimedia/Kingthings_Petrock.ttf");
}
body {
  font-size: 1rem;
}
#root {
  font-family: "Kingthings";
}

button {
  font-family: "Kingthings";
  background: rgba(0, 0, 0, 0);
  border: 0px;
  font-weight: 900;
  display: flex;
  align-items: center;
  cursor: pointer;
}
a {
  display: inline-block;
  position: relative;
}
